import { Link } from "react-router-dom";
import useWebsiteTitle from "../../../hooks/useWebsiteTitle";
import styles from "./WelcomeUser.module.css";

export default function WelcomeUser() {
  useWebsiteTitle(`Witaj madridisto! | RealMadryt.pl by Viniski`);
  
  return (
    <div className={styles.container}>
      <h1 className={styles.title}>Witaj na RealMadryt.pl by Viniski</h1>
      <h2 className={styles.subtitle}>
        Bardzo cieszymy si, e doczye do spoecznoci madridistas
      </h2>
      <p className={styles.paragraph}>
        Moesz teraz w peni korzysta z naszego portalu
      </p>
      <p className={styles.paragraph}>
        Sprawd, swojego maila z niespodziank specjalnie dla Ciebie i rabatem
        od naszego sponsora
      </p>
      <p className={styles.paragraph}>Hala Madrid y nada mas! </p>
      <p className={styles.paragraph}>
        <Link to={"/"}>
          Powr do strony gwnej
        </Link>
      </p>
    </div>
  );
}
